    margin: 1rem; }

/* Animation CSS */
.tab-appear {
  opacity: 0; }

.tab-enter {
  -webkit-transform: translateX(-80%);
      -ms-transform: translateX(-80%);
          transform: translateX(-80%);
  opacity: 0;
  z-index: 10; }

.tab-appear-active,
.tab-enter-active {
  -webkit-transition: all .5s ease-in-out;
  -o-transition: all .5s ease-in-out;
  transition: all .5s ease-in-out;
